#info {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
}

#info > div {
    position: relative;
    width: 100%;
    padding: 10px 25px;
    text-align: center;
    visibility: hidden;
    user-select: none;
}

#info.visible > div {
    visibility: visible;
}

#info h1 {
    text-align: center;
    text-transform: lowercase;
    font-family: Verdana;
    font-size: 1.5em;
    color: #f5f5f5;
    text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
             0px 8px 13px rgba(0,0,0,0.1),
             0px 18px 23px rgba(0,0,0,0.1);
    filter: blur(1px);
}

#info a {
    display: inline-flex;
    margin: 0 auto;
    font-family: Verdana;
    font-weight: 600;
    color: #222;
    fill: #222;
    background: #fff;
    text-decoration: none;
    padding: 10px;
    justify-content: center;
    transition: fill 0.2s ease;
    border-radius: 8px;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
    filter: blur(1px);
}

#info a svg {
    margin-left: 10px;
}

#info a:hover {
    color: #fff;
    fill: #fff;
    background: #333;
    filter: none;
}